import React from 'react'
import {CardScroll, Group, Header} from "@vkontakte/vkui";
import BalanceCard from "./BalanceCard";

const BalanceCards = (p) => {
    let d = p.p * p.e;
    let e = d * p.c.usd;
    let f = e / p.c.eur;
    return <Group header={<Header mode={"secondary"}>Счет</Header>}>
        <CardScroll size={"s"}>
            <BalanceCard a={'rub'} b={p.c.rub} c={p.b} d={e}/>
            <BalanceCard a={'usd'} b={p.c.usd} c={p.b} d={d}/>
            <BalanceCard a={'eur'} b={p.c.eur} c={p.b} d={f}/>
        </CardScroll>
    </Group>
}

export default BalanceCards;